<style scoped>
.inputwidth {
  width: 260px;
  padding-right: 30px;
}
.frombox {
  padding-top: 20px;
}
.iteminput {
  display: inline-block;
  padding-bottom: 10px;
}
.setpadding_bottom{
padding-bottom: 15px;
font-size: 16px;
}
.spanMust {
  color: red;
}
</style>
<template>
  <div class="cashbox">
    <div>
      <h1 class="setpadding_bottom">费用录入</h1>
      <p class="iteminput">
        <label for>&nbsp;收款人：</label>
        <!-- <Input v-model="Payee" placeholder="请输入收款人" class="inputwidth" /> -->
         <Select
            v-model="PayeeID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payeearr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payeearr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
      </p>
      <p class="iteminput" v-if="this.Payeearr.length>1&&PayeeID=='A235E8E7773EDB26697E7E771915197D'">
        <label for><span class="spanMust">*</span>名&nbsp;&nbsp;&nbsp;称：</label>
        <Input v-model="Anonymity" placeholder="请输入名称"  class="inputwidth" />
      </p>
      <p class="iteminput">
        <label for>&nbsp;付&nbsp;&nbsp;款&nbsp;&nbsp;人：</label>
        <!-- <Input v-model="Payer" placeholder="请输入付款人" class="inputwidth" /> -->
        <Select
            v-model="PayerID"
            filterable
            class="inputwidth"
            transfer
            :disabled="this.Payerarr.length==1?true:false"
          >
            <Option
              v-for="(item,index) in this.Payerarr"
              :value="item.ID"
              :key="index"
            >{{ item.Name }}</Option>
          </Select>
      </p>
      <p class="iteminput" v-if="this.Payerarr.length>1&&PayerID=='A235E8E7773EDB26697E7E771915197D'">
        <label for><span class="spanMust">*</span>名&nbsp;&nbsp;&nbsp;称：</label>
        <Input v-model="Anonymity" placeholder="请输入名称"  class="inputwidth" />
      </p>
      <p class="iteminput">
        <label for><span class="spanMust">*</span>科&nbsp;&nbsp;&nbsp;目：</label>
        <Select
          v-model="Subject"
          filterable
          class="inputwidth"
          transfer
          @on-change="change_subject"
        >
          <Option
            v-for="(item,index) in this.Subjectarr"
            :value="item.Name"
            :key="item.index"
          >{{ item.Name }}</Option>
        </Select>
      </p>

      <p class="iteminput" v-if="Labelfee==true">
        <label for><span class="spanMust">*</span>数&nbsp;&nbsp;&nbsp;量：</label>
        <Input v-model="Labeltotal" placeholder="请输入数量" @on-blur="Labelchange(Labeltotal)"  class="inputwidth" />
      </p>

      <p class="iteminput">
        <label for><span class="spanMust">*</span>币&nbsp;&nbsp;&nbsp;种：</label>
        <Select v-model="Currency" filterable class="inputwidth" transfer :disabled="disabledis">
          <Option
            v-for="(item,index) in Currencyarr"
            :value="item.value"
            :key="item.index"
          >{{ item.name }}</Option>
        </Select>
      </p>
      <p class="iteminput">
        <label for><span class="spanMust">*</span>{{lableName}}金额：</label>
        <Input v-model="LeftPrice" placeholder="请输入金额" @on-blur="testprice(LeftPrice)" :disabled="disabledis"  class="inputwidth" />
      </p>
      <!-- <p class="iteminput">
             <label for="">金&nbsp;&nbsp;&nbsp;&nbsp;额：</label><Input v-model="Currency" placeholder="请输入金额" class="inputwidth" />
      </p>-->
      <Button type="primary" size="small"  icon="ios-cloud-upload" @click="SeletUploadFile">传照</Button>
      <Button size="small"  type="primary" icon="md-reverse-camera" @click="FormPhoto">拍照</Button>
      <h3 style="padding-bottom:5px">文件列表：</h3>
      <ul v-if="Files.length>0">
        <li v-for="item in Files">
          {{item.CustomName}}
          <Icon type="ios-trash" @click="handleRemove(item)"/>
        </li>
      </ul>
      <div v-else>暂无数据</div>
      <div style="padding:15px 0">
        <Button type="primary" size="small" icon='md-checkmark' @click="submit_btn">提交</Button>
      </div>
      
    </div>
    <div>
      <h1 class="setpadding_bottom">流水列表</h1>
      <Table stripe  :columns="columns1" :data="Records" max-height="380">
        <template slot-scope="{ row,index }" slot="indexs">
          <span>{{index+1}}</span>
        </template>
        <template slot-scope="{ row }" slot="type">
          <span>{{row.Conduct}}</span>
        </template>
        <template slot-scope="{ row }" slot="Subject">
          <span>{{row.Subject}}</span>
        </template>
        <template slot-scope="{ row }" slot="valuationCurrency">
          <span>{{row.RecordPrice}}</span>
        </template>
        <template slot-scope="{ row }" slot="SettlementCurrency">
          <span>{{row.SettlePrice}}</span>
        </template>
        <template slot-scope="{ row }" slot="createdata">
          <span>{{row.CreateDate}}</span>
        </template>
        <template slot-scope="{ row }" slot="user">
          <span>{{row.Creator}}</span>
        </template>
      </Table>
      <!-- <div style="padding-top:15px;text-align: right;">
        <Page :total="100" show-elevator />
      </div> -->
    </div>
  </div>
</template>
<script>
import { currency, subject,submitfee,CgDeleteFiles} from "../../api/CgApi";
import{ SeletUploadFile,FormPhoto} from '../../js/browser'
export default {
  props: ["lableName","Currencyarr", "Subjectarr",'Payeearr','Payerarr','OrderID','WaybillID','Records','conduct','Files','delbookeep'],
  data() {
    return {
      Labeltotal:'',
      Labelfee:false, //是否是处理标签费
      Subject: "", //科目
      Currency: 3, //币种 ,默认是港币
      LeftPrice: "", //实收 实付金额
      disabledis: false, //根据科目判断是否可修改
      columns1: [
        {
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: "业务",
          slot: "type",
          align: "center"
        },
        {
          title: "科目",
          slot: "Subject",
          align: "center"
        },
        {
          title: "计价金额",
          slot: "valuationCurrency",
          align: "center"
        },
        {
          title: "结算金额",
          slot: "SettlementCurrency",
          align: "center"
        },
        {
          title: "发生时间",
          slot: "createdata",
          align: "center",
          width: 160,
        },
        {
          title: "录入人",
          slot: "user",
          align: "center"
        }
      ],
      PayeeID:"",
      PayerID:"",
      Anonymity:null,//名称
      // Files:[],
      showAnonymity:false,
    };
  },

  mounted() {
    //  window["PhotoUploaded"] = this.changed;
  },
  methods: {
    testprice(value){  //验证金额
        if(value!=''){
            // var testis=/(^([-]?)[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^([-]?)(0){1}$)|(^([-]?)[0-9]\.[0-9]([0-9])?$)/; 
           var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
           var Result = testis.test(value)
            if(Result==false){
                this.LeftPrice='';
                this.$Message.error('请输入正确的金额，金额保留到小数点后两位');
            }else{

            }
        }
     },
    Labelchange(val){
      if(val!=''){
            // var testis= /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/
            var testis=/^[0-9]*$/
            var Result = testis.test(val)
            if(Result==false){
                this.LeftPrice='';
                this.$Message.error('请输入处理标签的数量');
            }else{
               this.disabledis = true;
               this.Currency = 1;
               if(val<=10){
                 this.LeftPrice=100
               }else{
                 var num1=val-10
                 var number=num1*5
                 this.LeftPrice=100+number
               }
               
               
            }
      }
     },
    change_subject(value) {
      if(value=='处理标签费'){
          this.Labelfee=true;
      }else{
         this.Labelfee=false;
      }
      var newarr = [];
      newarr = this.Subjectarr.filter(
        item => item.Name == value && item.PayQuote.Price != null
      );
      console.log(newarr);
      var obj = newarr[0];
      if (newarr.length > 0) {
        this.disabledis = true;
        this.Currency = obj.PayQuote.Currency;
        this.LeftPrice = obj.PayQuote.Price;
        // this.rightprice=obj.PayQuote.Price;   //实收实付
        // this.rightprice="";   //实收实付
        // this.leftprice=obj.PayQuote.Price;
        // this.formdata.currency=obj.PayQuote.Currency;
      } else {
        if(value=='处理标签费'){
          this.disabledis = true;
          this.Currency = 1
        }else{
          this.disabledis = false;
          this.Currency = 3
          this.LeftPrice = ""
        }
        
      }
    },
    currency_subject() {
      //币种
      currency().then(res => {
        this.Currencyarr = res.obj;
      });
    },
    submit_btn() {
      var id='A235E8E7773EDB26697E7E771915197D'
      if(this.Subject==''||this.Currency==''||this.LeftPrice==''||((this.PayeeID==id||this.PayerID==id)&&(this.Anonymity==null||this.Anonymity==''))){
        this.$Message.error('请输入必填项');
      }else{
          if(this.Subject=='处理标签费'&&this.Labeltotal==''){
           this.$Message.error('请输入处理标签的个数');
          }else{
            var Source=null
            if(sessionStorage.getItem("WareHouseName").indexOf('深圳')!=-1){
              Source='深圳库房'
            }else{
              Source='香港库房'
            }
            var conductdes=null
            if(this.conduct==30||this.conduct==35||this.conduct==60){
              conductdes='代报关'
            }else{
              conductdes='代仓储'
            }
            var fee = {
              Currency: this.Currency, //币种
              FeeType: this.$route.query.type, //类型 in out 
              Files: this.Files, //文件
              LeftPrice:this.LeftPrice, //应收 应付
              OrderID:this.OrderID, //订单id
              Payee: this.PayeeID, //收款人
              Payer: this.PayerID, //付款人
              RightPrice:'', //实收实付
              Subject: this.Subject, //科目
              WaybillID:this.WaybillID,
              Quantity:this.Labeltotal,//标签给叔
              Anonymity:this.Anonymity,//匿名时候的名称
              Conduct:conductdes,//业务类型
              Source:Source
            };
            console.log(fee)
            submitfee(fee).then(res => {
                  if(res.success==true){
                      this.$Message.success('保存成功');
                      this.Subject=""; //科目
                      this.Currency=3; //币种 ,默认是港币
                      this.LeftPrice= "";//实收 实付金额
                      this.Labeltotal=''
                      this.Files=[]
                      this.$emit('fatherMethod');
                      this.$emit('delbookeep')
                  }else{
                      this.$Message.error('保存失败，请核实信息是否正确');
                  }
            });
         }
      }
      
    },
    SeletUploadFile(){
      var data = {
        SessionID: 'Bookkeeping',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
        SeletUploadFile(data);
    },
    FormPhoto(){
      var data = {
        SessionID:'Bookkeeping',
        AdminID: sessionStorage.getItem("userID"),
        Data:{
          WayBillID:null,
          WsOrderID:null,
        }
      };
      FormPhoto(data)
    },
    //后台调用winfrom 拍照的方法
    changed(message) {
      var imgdata = message[0];
      var newfile = {
        CustomName: imgdata.FileName,
        ID: imgdata.FileID,
        Url: imgdata.Url,
      };
      this.Files.push(newfile)
    },
       //删除上传文件按
    handleRemove(file) {
       var data={
          id:file.ID
        }
       CgDeleteFiles(data).then(res=>{
          console.log(res)
          if(res.Success==true){
            this.$Message.success('删除成功')
             this.Files.splice(this.Files.indexOf(file), 1);
          }else{
            this.$Message.error('删除失败')
          }
      })
    },
  }
};
</script>


